.doubt {
	
	position: relative;
	@media @mobile {
       margin-top: 0px;
       background: url('@{dir}bg-mob-2.jpg') no-repeat top center; 
     	-webkit-background-size: cover;
	    -moz-background-size: cover;
	    -o-background-size: cover;
	    background-size: cover; 

	    outline: 1px solid #00e4fc;
	    -webkit-outline: 1px solid #00e4fc;
	    -moz-outline: 1px solid #00e4fc;
	    -o-outline: 1px solid #00e4fc;
	    outline-offset: 15px;
	    -webkit-outline-offset: 15px;
	    -moz-outline-offset: 15px;
	    -o-outline-offset: 15px;
	    padding: 70px 0;
    }
    @media @tablet {
    	padding: 0;
		margin-top: 130px;
		background: none;
		outline: none;
		outline-offset: 0px;
    }
	
	// padding-bottom: 150px;
	.figure-2 {
		position: absolute;
		top: 260px;
		left: 0;
		z-index: -1;
		// width: 400px;
		// height: auto;
		@media @mobile {
	       height: auto;
	       width: 100%;
	    }
	    @media @tablet {
			width: auto;
	    }
	}
	.doubt-container {
		position: relative;
		z-index: 2;
		.doubt-left {
			display: inline-block;
			vertical-align: top;
			padding-left: 20px;
			h2 {
				
				font-weight: 300;
				// color: #425160;	
				margin-bottom: 10px;
				
				@media @mobile {
	       			text-align: left;
	       			font-size: 30px;
			    }
			    @media @tablet {
					
					font-size: 35px;
			    }
			}
			h3 {
				font-size: 35px;
				font-weight: 700;
				// color: #dbb970;	
				margin-bottom: 35px;
				text-align: left;
				@media @mobile {
	       			
	       			font-size: 30px;
			    }
			    @media @tablet {
					
					font-size: 35px;
			    }
			}
			h4 {
				font-size: 20px;
				font-weight: 500;
				// color: #81878b;	
				margin-bottom: 5px;
				text-align: left;
			}
			ul {
				margin-bottom: 40px;
				@media @mobile {
	       
			    }
			    @media @tablet {

			    }
			    @media @desktop {	
					width: 480px;
			    }
			    @media @desktop-xl {
			    	width: auto;
			    }
				li {
					list-style: disc;
					font-size: 20px;
					color: #93dcfd;	
					@media @mobile {
	       				color: #fff;	
				    }
				    @media @tablet {
						color: #93dcfd;	
				    }
					span {
						font-size: 16px;
						font-weight: 300;
						color: #2f4a7a;	
						@media @mobile {
	       					font-weight: 400;
					    }
					    @media @tablet {
							font-weight: 300;
					    }
					}
				}
			}
			h5 {
				font-size: 20px;
				font-weight: 300;
				
				margin-bottom: 10px;
				text-align: left;
			}
			h6 {
				font-size: 20px;
				font-weight: 700;
				
				margin-bottom: 0px;
				text-align: left;
			}
		}


		.doubt-right {
			
			background: #fff;	
			padding: 10px;
			
			border-radius: 15px;
			-moz-border-radius: 15px;
			-webkit-border-radius: 15px;
			// -webkit-box-shadow: 10px 10px 58px 2px rgba(192,197,206,0.45);
			// -moz-box-shadow: 10px 10px 58px 2px rgba(192,197,206,0.45);
			// box-shadow: 10px 10px 58px 2px rgba(192,197,206,0.45);
				-webkit-box-shadow: 5px 27px 51px rgba(95,82,122,.15);
			-moz-box-shadow: 5px 27px 51px rgba(95,82,122,.15);
			box-shadow: 5px 27px 51px rgba(95,82,122,.15);
			@media @mobile {
		       width: 300px;
		       margin: 0 auto;
		       margin-top: 40px;
		    }
		    @media @tablet {
				width: 445px;
		    }
		    @media @desktop {
		    	margin: 0;
		    	margin-top: 40px;
		    	width: auto;
		    	float: right;	
				margin-right: 0px;
		    }
		    @media @desktop-xl {
		    	margin-right: 70px;    
		    }

			.border {
				border: 1px solid #93dcfd;
				padding: 30px 20px 40px;
				border-radius: 10px;
				-moz-border-radius: 10px;
				-webkit-border-radius: 10px;
			}
			h3 {
				
				font-size: 30px;
				font-weight: 500;
				
				margin-bottom: 15px;
				@media @mobile {
	       			font-size: 26px;
			    }
			    @media @tablet {
					font-size: 30px;
			    }
			}
			p {
				text-align: center;
				font-size: 18px;
				font-weight: 300;
				
				margin-bottom: 15px;
				span {
					font-weight: 500;
				}
			}

			.input {
		            display: block;
		            width: 339px;
		            height: 45px;
		            -webkit-border-radius: 10px;
		            -moz-border-radius: 10px;
		            border-radius: 10px;
		            background: #f1f9ff;
		            
		            border: none;
		            
		            color: #2f4a7a;
		            padding: 0px 20px 0;
		            outline: none;
		            overflow: hidden;
		            line-height: 29px;
		            font-size: 18px;            
		            font-weight: 300;
		            margin: 0 auto;
		            margin-bottom: 20px;

		            position: relative;




		           
		            
		            @media @mobile {
		                width: 100%;
		               
		            }
		            @media @tablet {
		               width: 339px;
		            }
		      //       &:after {
		      //       content: "";
		      //       position: absolute;
		//          display: block; 
		            // right: -3px;
		    //      height: 3px;
		      //        background-image: -webkit-gradient(linear, 0 0, 100% 0, from(#ffea9c), to(#fec200));
		            //   background-image: -webkit-linear-gradient(left, #ffea9c, #fec200);
		            //   background-image: -moz-linear-gradient(left, #ffea9c, #fec200);
		            //   background-image: -o-linear-gradient(left, #ffea9c, #fec200);
		      //       }
		       

		        }
		        .input::-webkit-input-placeholder {
		            font-size: 18px;
		            font-weight: 300;
		            color: #2f4a7a !important;
		            @media @mobile {
		                
		            }
		            @media @tablet { 
		               
		                
		            }

		        }
		        .input::-moz-placeholder {
		             font-size: 18px;
		            font-weight: 300;
		            color: #2f4a7a !important;
		            

		        }
		        .input:-moz-placeholder {
		             font-size: 18px;
		            font-weight: 300;
		            color: #2f4a7a !important;
		            

		        }
		        .input:-o-placeholder {
		             font-size: 18px;
		            font-weight: 300;
		            color: #2f4a7a !important;
		            
		        }
		        .input:-ms-input-placeholder {
		             font-size: 18px;
		            font-weight: 300;
		            color: #2f4a7a !important;
		           

		        }
            

		}
		.btn-doubt {
			
			@media @mobile {
	       		width: 238px;
		    }
		    @media @tablet {
				width: 366px;
		    }
			span {
				@media @mobile {
	       			display: none;
			    }	
			    @media @tablet {
					display: inline-block;
			    }
			}
		}
	}
}