@dir: 						    	"../images/";
@dir-fonts: 				  		"../fonts/";

@html-size-base: 					10px;

@fs: 						      	1.6rem;
@fss: 						    	2rem;
@fsb: 						    	1.6rem;
@h1: 						      	4rem;
@h2: 						      	3rem;
@h3: 						      	2rem;
@h4: 						      	2rem;
@h5: 						      	2rem;
@h6: 						      	2rem;



@lh: 						      	1.2;

@ff1: 								'Rubik', sans-serif;
@ff2: 								'PT Sans', sans-serif;

@black: 							#2f4a7a;
