.background-2 {

    
    
    position: relative;
    z-index: 1;
    @media @mobile {
       
    }
    @media @tablet {
        margin-top: -46px;
        background: url('@{dir}background-2.png') no-repeat top center; 
    }
    @media @desktop {   
        background: url('@{dir}background-2.png') no-repeat top center; 
    }
    @media @desktop-xl {
        background: url('@{dir}background-2.png') no-repeat top center;  
    }
    @media @desktop-xl-2 {
        background: url('@{dir}background-2.png') no-repeat top center;  
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
    }
}


#structure {
	
    @media @mobile {
       padding-top: 70px;
       padding-bottom: 70px;
    }
    @media @tablet {
        padding-top: 305px;
        padding-bottom: 0;
    }
 	h2 {
 		font-size: 35px;
		font-weight: 700;
		
		margin-bottom: 50px;
		
		
        @media @mobile {
           font-size: 30px;
           text-align: center;
        }
        @media @tablet {
            font-size: 35px;
            padding-left: 140px;
            text-align: left;
        }
 	}
 	#accordion {
 		
 		margin: 0 auto;
 		text-align: left;
        @media @mobile {
           width: 100%;
        }
        @media @tablet {
            width: 720px;
        }
        @media @desktop {   
            width: 890px;
        }
 		.ui-accordion-header {
 			 width: 100%;
            height: auto;
            position: relative;
            margin: 0;
            padding: 0;
            border: none;
            border-radius: 0;
			       background: #fff;
            font-size: 20px;
            font-weight: 700;
            color: #2f4a7a;
            padding: 12px 70px 12px;
            margin-top: 10px;
            
            text-align: left;
            border: 1px solid #93dcfd;
            border-radius: 10px;
			-moz-border-radius: 10px;
			-webkit-border-radius: 10px;

			webkit-box-shadow: 0 6px 21px 1px rgba(82, 82, 82, 0.16);
		    -moz-box-shadow: 0 6px 21px 1px rgba(82, 82, 82, 0.16);
		    box-shadow: 0 6px 21px 1px rgba(82, 82, 82, 0.16);
            @media @mobile {
                font-size: 16px;
                padding: 12px 40px 12px;
            }
            @media @tablet {
                font-size: 20px;
                padding: 12px 70px 12px;
            }
		    .ui-icon {
                background: url('@{dir}minus.png') no-repeat top center !important;
                width: 17px;
                height: 5px;
                position: absolute;
                
                transition: all 0.2s ease;
                -webkit-transition: all 0.2s ease;
                -ms-transition: all 0.2s ease;
                @media @mobile {
                    top: 58%;
                    left: 10px;
                }
                @media @tablet {
                    top: 29px;
                    left: 29px;
                }
                
            }

 		}
 		.ui-accordion-header-active {
            .ui-icon {
                background: url('@{dir}minus-2.png') no-repeat top center !important;
                width: 17px;
                height: 17px;
                position: absolute;
                top: 23px;
                left: 29px;
                transition: all 0.2s ease;
                -webkit-transition: all 0.2s ease;
                -ms-transition: all 0.2s ease;
                @media @mobile {
                    top: 50%;
                    left: 10px;
                }
                @media @tablet {
                    top: 23px;
                    left: 29px;
                }
                
            }
 		}
		.ui-accordion-content {
            position: relative;
            z-index: 0;
            background: none !important;

            -webkit-border-radius: 0px;
            -moz-border-radius: 0px;
            border-radius: 0px;
            border: none;
            
            margin-top: 0px;
            @media @mobile {
                padding: 0px 20px 0px 40px;
                margin: 20px 0 ;

            }
            @media @tablet {
                padding: 20px 70px 20px 70px;
                margin: 0 ;
            }
            p {
                font-size: 16px;
                color: #2f4a7a;
                font-weight: 300;
                @media @mobile {
                    font-size: 15px;
                }
                @media @tablet {
                    font-size: 16px;
                }

            }
        }
        .ui-accordion-content-active {
            overflow: visible;
            @media @mobile {
                height: auto; 
                // height: 200px; 
                // min-height: 200px
                
            }
            @media @tablet {
                height: 160px ;
                
               
            }
            @media @desktop {
                height: 140px ;
            }
            @media @desktop-xl {
                height: 137px ;
            }
   		}

 		
 	}
 	.btn-structure {
 		margin: 70px auto 0;

 	}
}