.remodal-close { 
    top: 12px !important;
    right: 15px !important;
    color: #fff !important;
    font-weight: 900;
    @media @mobile {
       width: 10px !important;
        height: 16px !important;
    }
     @media @tablet { 
        width: 35px !important;
        height: 30px !important;
    }
    &:before {
        @media @mobile {
            font-size: 30px !important;
            top: -7px !important;
            left: -12px !important;
        }
         @media @tablet { 
            font-size: 50px !important;
            top: -0px !important;
            left: -0px !important;
        }
    }
}

