.leading {
    position: relative;
    z-index: 0;
    // margin-top: -70px;
    @media @mobile {
       padding: 70px 0;
       text-align: center;
    }
    @media @tablet {
        padding:  0;
        text-align: left;
    }


    .figure-3 {
        position: absolute;
        top: -760px;
        left: 0;
        z-index: -1;
        @media @mobile {
           height: auto;
           width: 100%;
        }
        @media @tablet {

        }
        @media @desktop {   
            width: auto;
        }
        @media @desktop-xl {
            width: 600px;
            top: -360px;
        }
        @media @desktop-xl-2 {
            width: auto;
            top: -760px;
        }
        @media @desktop-xll {
            
            
        }
    }
    .figure-4 {
        position: absolute;
        top: 200px;
        right: 0;
        z-index: -1;
        @media @mobile {
           height: auto;
           width: 100%;
        }
        @media @tablet {

        }
        @media @desktop {   
            width: auto;
        }
    }
    
    .container {
        
        @media @mobile {
           
        }
        @media @tablet {
            padding-left: 30px;
        }
        @media @desktop {   
            padding-left: 100px;
        }
    }
    h2 {
        font-size: 25px;
        font-weight: 700;
        // color: #dbb970; 
        margin-bottom: 10px;
        
        @media @mobile {
            text-align: center;
        }
        @media @tablet {
            text-align: left;
        }
    }
    h3 {
        font-size: 42px;
        font-weight: 300;
        // color: #425160; 
        margin-bottom: 25px;
        text-align: left;
        @media @mobile {
            font-size: 32px;
            text-align: center;
        }
        @media @tablet {
            font-size: 42px;
            text-align: left;
        }
    }
    .leading-img {
        @media @mobile {
            height: auto;
            width: 300px;
            margin: 0 auto;
            margin-bottom: 20px;
            // display: block;
        }
        @media @tablet {
            // display: inline-block;
            // vertical-align: top;
            width: 350px;
            margin: 0;
            margin-bottom: 0;

        }
        @media @desktop {   
            width: auto;
        }
    }
    ul {
         display: inline-block;
            vertical-align: middle;
        
        // @media @mobile {
        //    width: 300px;
        //    margin: 0 auto;
        // }
        // @media @tablet {
        //     display: inline-block;
        //     vertical-align: middle;
        //     width: auto;
        //     margin: 0;
        // }
        li {
            img {
                margin-right: 15px;
                
            }
            font-size: 16px;
            font-weight: 300;
            color: #2f4a7a;
            margin-bottom: 20px;
            text-align: left;
            &:nth-child(2), &:nth-child(3) {
                
                @media @mobile {
           
                }
                @media @tablet {
                    padding-left: 10px;
                }
            }
        }
    }
    h4 {
        font-size: 19px;
        // color: #dbb970;
        font-weight: 500;
        margin-bottom: 15px;
    }
    p {
        
       font-size: 16px;
        // color: #6a7175;
        font-weight: 300;
        @media @mobile {
           
        }
        @media @tablet {
            width: 475px;
        }
    }
}