.footer {
   
    background: #222a2f;
    
    
    @media @mobile {
       text-align: center;
       padding: 40px 0 40px;
    }
    @media @tablet {
        
        
    }
    @media @desktop {
       
    }
    .container {
        @media @mobile {
           height: auto;
        }
        @media @tablet {

        }
        @media @desktop {   
            
        }
        @media @desktop-xl {   
            
        }
        @media @desktop-xll {
            
            
        }
    }
    
    .item {
        
        text-align: left;
        margin: 0 20px;
        @media @mobile {
           text-align: center;
           margin: 0 0px 50px;
        }
        @media @tablet {
            display: inline-block;
            vertical-align: top;
            text-align: left;
            margin: 0 12px 45px;
        }
        @media @desktop {   
            margin: 0 12px;
        }
        @media @desktop-xl {
            margin: 0 20px;    
        }
        h3 {
            font-size: 23px;
            font-weight: 700;
            color: #fff; 
            margin-bottom: 15px;
            
            @media @mobile {
                text-align: center;
            }
            @media @tablet {
                text-align: left;
            }
            
        }
        p {
            font-size: 16px;
            font-weight: 300;
            color: #d4d9dd;
            margin-bottom: 10px;
        }
        a {
            display: block;
            font-size: 16px;
            font-weight: 300;
            color: #d4d9dd;
        }
        .mail {
            margin-top: 10px;
        }
        ul {
            display: inline-block;
            vertical-align: top;
            position: relative;
            z-index: 1;
            padding-left: 28px;
            li {
                margin-bottom: 7px;
            }
            .network-bg {
                position: absolute;
                top: 17px;
                left: -35px;
                z-index: -1;
            }
        }
        .skype {
            display: inline-block;
            vertical-align: middle;
            margin-left: 50px;
            padding-top: 53px;
            text-align: center;
            p {
                padding-top: 10px;
            }
        }
        .btn-footer {
            width: 220px;
            height: 55px;
            font-size: 16px;
            font-weight: 500;
            color: #fff;
            

            webkit-box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0);
            -moz-box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0);
            box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0);
            &:hover {
                background-image: linear-gradient(to left, #ff8214, #ff681f);
                webkit-box-shadow: -2px 15px 28px -12px rgba(244, 118, 107, 0);
                -moz-box-shadow: -2px 15px 28px -12px rgba(244, 118, 107,  0);
                box-shadow: -2px 15px 28px -12px rgba(244, 118, 107,  0);
            }
            @media @mobile {
                
                margin: 0 auto;
                margin-top: 35px;
            }
            @media @tablet {

            }
            @media @desktop {  
                margin: 0 ;
                margin-top: 35px;
            }

        }
    }
    .item-1 {
        text-align: center;
        
        @media @mobile {
           
        }
        @media @tablet {
            display: block;
        }
        @media @desktop {   
            display: inline-block;
            vertical-align: top;
        }
        @media @desktop-xl {
             margin-right: 80px;   
        }
        .logo {
            margin-bottom: 10px;
            display: inline-block;
            vertical-align: top;
        }
         p {
            color: #fff;
         }
    }
    .item-2 {
        padding-right: 10px;
        @media @mobile {
           
        }
        @media @tablet {

        }
        @media @desktop {   
            width: 250px;
        }
        @media @desktop-xl {
            width: auto;    
        }
    }
    .item-4 {
        @media @mobile {
           text-align: center;
        }
        @media @tablet {

        }
        @media @desktop {   
            width: 250px;
            text-align: left;
        }
        @media @desktop-xl {
            width: auto;    
        } 
        h3 {
            
            @media @mobile {
                text-align: center;
            }
            @media @tablet {

            }
            @media @desktop {   
                text-align: left;
            }
            
        }
        p {
            br {
                @media @mobile {
                    display: none;
                }
                @media @tablet {
                    display: inline-block; 
                }
                @media @desktop {   
                    display: none;
                }
                @media @desktop-xl {
                    display: inline-block; 
                }
            }
        }
    }
    
    
      


    
}