

.reviews {
    
    padding-bottom: 60px;
    @media @mobile {
       padding-top: 70px;
    }
    @media @tablet {
        padding-top: 0;
        margin-top: 190px;
    }
    h2 {
        font-size: 35px;
        font-weight: 700;
        // color: #f5e18f; 
        margin-bottom: 40px;
        
        
        @media @mobile {
            font-size: 30px;
            text-align: center;
        }
        @media @tablet {
            font-size: 35px;
            text-align: left;
            padding-left: 35px;
        }
    }
    .reviews-slider {
        margin: 0 auto;
       
        @media @mobile {
           width: 100%;

        }
        @media @tablet {

        }
        @media @desktop {   
            
        }
        @media @desktop-xl {
           width: auto;     
        }
        .slick-slide {
            outline: none;
            padding-bottom: 70px;
            @media @mobile {
               text-align: center;
            }
            @media @tablet {
                
            }
            @media @desktop {
                text-align: center;
            }
            img {
                border-radius: 50% ; 
                -webkit-box-shadow: 3px 17px 40px rgba(0,0,0,.25);
                    -moz-box-shadow: 3px 17px 40px rgba(0,0,0,.25);
                    box-shadow: 3px 17px 40px rgba(0,0,0,.25);


                @media @mobile {
                    width: 200px;
                    height: auto;                 
                    margin: 0 auto;
                    // margin-right: 15px;
                   
                }
                @media @tablet {
                    width: 250px;                   
                    margin: 0 auto;

                }
                @media @desktop {
                    display: inline-block;
                    vertical-align: middle;  
                    width: 300px;
                    margin-top: 30px;
                   
                }
                @media @desktop-xl {
                    width: 315px;

                }
            }
            .text {
                
                
                
                @media @mobile {
                    text-align: center;
                }
                @media @tablet {

                }
                @media @desktop { 
                    display: inline-block;
                    vertical-align: middle;  
                    width: 50%;   
                    text-align: left;
                    margin-left: 35px;
                }
                h3 {
                    font-size: 25px;
                    font-weight: 500;
                    // color: #fff; 
                    margin-bottom: 30px;
                    
                    @media @mobile {
                        text-align: center;
                        margin-top: 30px;
                        margin-bottom: 20px;
                        font-size: 22px;
                    }
                    @media @tablet {
                        font-size: 25px;
                    }
                    @media @desktop {   
                        text-align: left;
                        margin-top: 0;
                        margin-bottom: 30px;
                    }
                }
                p {
                     font-size: 16px;
                    font-weight: 300;
                    // color: #fff; 
                }
            }
        }
        .slick-arrow {
            z-index: 3;
           
            transition: all 0.5s ease;
            -webkit-transition: all 0.5s ease;
            -ms-transition: all 0.5s ease;
            &:hover {
                 
            }
            
        }
        .slick-next {
                right: -35px;
                top: 50%;
                background: url('@{dir}next.png') no-repeat top center;  
                -webkit-background-size: cover;
                -moz-background-size: cover;
                -o-background-size: cover;
                background-size: cover;
                width: 44px;
                height: 81px;

                @media @mobile {
                    right: -0px;
                    top: 13%;
                }
                @media @tablet {
                    top: 25%;
                }
                @media @desktop {   
                    top: 44%;
                }
                @media @desktop-xl {
                    right: 0px;    
                }
                 &:before {
                    content: '' !important;
                    // font-family: 'slick';
                 //    font-size: 22px;
                 //    line-height: 1;
                 //    color: #000;
                 //    background: #fff;
                }
            }
            .slick-prev {
                left: -35px;
                right: auto;
                top: 50%;
                background: url('@{dir}prev.png') no-repeat top center;  
                -webkit-background-size: cover;
                -moz-background-size: cover;
                -o-background-size: cover;
                background-size: cover;
                width: 44px;
                height: 81px;
                @media @mobile {
                    left: -0px;
                    top: 13%;
                }
                @media @tablet {
                    top: 23%;
                }
                @media @desktop {   
                    top: 44%;
                }
                @media @desktop-xl {
                    left: 0px;    
                }
                &:before {
                    content: '' !important;
                    // font-family: 'slick';
                 //    font-size: 22px;
                 //    line-height: 1;
                 //    color: #000;
                 //    background: #fff;
                }
            }
            .slick-dots {
                @media @mobile {
                    bottom: 0px;
                }
                @media @tablet {
                    bottom: 0px;
                }
                @media @desktop {   
                    bottom: 0px;
                }
                @media @desktop-xl {
                    bottom: 0px;
                }
                @media @desktop-xl-2 {
                    bottom: 0px;
                }
                li {
                    vertical-align: middle;
                    border: 2px solid #fff;
                    background: #fff;
                    border-radius: 50%;
                    opacity: 1;
                    width: 16px;
                    height: 16px;
                    // transition: all 0.2s ease;
                    // -webkit-transition: all 0.2s ease;
                    //     -ms-transition: all 0.2s ease;

                        -webkit-box-shadow: 1px 3px 8px rgba(22,22,22,.22);
                        -moz-box-shadow: 1px 3px 8px rgba(22,22,22,.22);
                        box-shadow: 1px 3px 8px rgba(22,22,22,.22);

                        margin: 0 10px;
                       
                        
                     button {
                        &:before {
                            font-size: 0 !important;
                            color: #fff;
                        }
                     }
                }
                .slick-active {
                    width: 23px;
                    height: 23px;
                    border: 3px solid #fff;
                    opacity: 1;
                    background: #93dcfd;
                    margin: 0px 7px 0;
                    
                   
                }
            }
    }
    
}