.video-block {
	
	
	
	
	position: relative;
	@media @mobile {
     	padding-top: 70px;
     	background: url('@{dir}bg-mob-2.jpg') no-repeat top center; 
     	-webkit-background-size: cover;
	    -moz-background-size: cover;
	    -o-background-size: cover;
	    background-size: cover; 
	    padding-bottom: 70px;

	    outline: 1px solid #00e4fc;
	    -webkit-outline: 1px solid #00e4fc;
	    -moz-outline: 1px solid #00e4fc;
	    -o-outline: 1px solid #00e4fc;
	    outline-offset: 15px;
	    -webkit-outline-offset: 15px;
	    -moz-outline-offset: 15px;
	    -o-outline-offset: 15px;
	    
    }
    @media @tablet {
    	// background: url('@{dir}header.png') no-repeat top center;  
		// background: url('@{dir}video.png') no-repeat top center;
		// -webkit-background-size: cover;
	 //    -moz-background-size: cover;
	 //    -o-background-size: cover;
	 //    background-size: cover;
	 // background-size: 100% 100%;
		margin-top: -50px;
		padding-top: 0px;
		padding-bottom: 0px;
		height: 750px;
		background: none;
		outline: none;
		outline-offset: 0px;
    }
    @media @desktop {	
    	background: url('@{dir}video.png') no-repeat top center;
    	-webkit-background-size: cover;
	    -moz-background-size: cover;
	    -o-background-size: cover;
	    background-size: cover;
		margin-top: -50px;
		height: 1030px;
		padding-top: 135px;
    }
    @media @desktop-xl {
    	background: none;
    	height: 1225px;
	    padding-top: 50px;
	    margin-top: -150px;
    }
    @media @desktop-xl-2 {
    	height: 1300px;
		background: url('@{dir}video.png') no-repeat top center;
		padding-top: 160px;
		margin-top: -200px;
    }
    @media @desktop-xll {
    	
    	
    }
	.container {
		@media @mobile {
	       height: 100%;
	    }
	    @media @tablet {
			background: url('@{dir}video.png') no-repeat top center;  
	    	-webkit-background-size: cover;
		    -moz-background-size: cover;
		    -o-background-size: cover;
		    background-size: cover;
		    padding-top: 85px;
	    }
	    @media @desktop {	
			background: none;
			padding-top: 0px;
	    }
	    @media @desktop-xl {
	    	background: url('@{dir}video.png') no-repeat top center;  
	    	-webkit-background-size: cover;
		    -moz-background-size: cover;
		    -o-background-size: cover;
		    background-size: cover;
		    padding-top: 180px;
	    }
	    @media @desktop-xl-2 {
    		background: none;
    		padding-top: 0;
	    }
	    @media @desktop-xll {
	    	
	    	
	    }
	}

	h2 {
		font-size: 40.5px;
		font-weight: 700;
		
		margin-bottom: 15px;
		@media @mobile {
	       font-size: 28px;
	    }
	    @media @tablet {
	    	color: #fff;
			font-size: 30px;
	    	margin-bottom: 0px; 
	    }
	    @media @desktop {	
			font-size: 35.5px;
	    	margin-bottom: 0px; 
	    }
	    @media @desktop-xl {
	    	font-size: 35.5px;
	    	margin-bottom: 0px;  
	    	padding-top: 40px;  
	    }
	    @media @desktop-xl-2 {
	    	font-size: 40.5px;
    		margin-bottom: 15px;

	    }
	    @media @desktop-xll {
	    	
	    	
	    }
		span {
			font-size: 30px;
		}
	}
	h3 {
		font-size: 50.5px;
		font-weight: 300;
		// color: #ffd987;
		
		
		margin-bottom: 30px;
		@media @mobile {
	       font-size: 32px;
	    }
	    @media @tablet {
	    	text-align: right;
			font-size: 35px; 
	    	padding-right: 100px; 
	    	margin-bottom: 20px;
	    }
	    @media @desktop {	
			font-size: 40.5px; 
	    	padding-right: 200px;
	    	margin-bottom: 30px; 
	    }
	    @media @desktop-xl {
	    	font-size: 40.5px; 
	    	padding-right: 250px;  
	    }
	    @media @desktop-xl-2 {
    		font-size: 50.5px;
    		padding-right: 200px;
	    }
	    @media @desktop-xll {
	    	
	    	
	    }
		
	}
	.video-after {
		position: relative;
		.video-bg {
			position: absolute;
			bottom: -90px;
			left: 50%;
			margin-left: -468px;
			@media @mobile {
	       		display: none;
		    }
		    @media @tablet {

		    }
		    @media @desktop {	
		    	display: block;
				width: 700px;
	    		margin-left: -350px;
	    		bottom: -60px;
		    }
		    @media @desktop-xl {
		    	width: 700px;
	    		margin-left: -350px;
	    		bottom: -60px;
		    }
		    @media @desktop-xl-2 {
	    		width: auto;
	    		margin-left: -468px;
	    		bottom: -90px;
		    }
		    @media @desktop-xll {
		    	
		    	
		    }
		}
	}
	.video {
    	border: 2px solid #fff;
        // background: url('@{dir}video-bg.png') no-repeat top left;
        position: relative; 
        padding-bottom: 56.25%; 
        height: 0; 
        overflow:hidden;
        width: 853px;

        margin: 0 auto;
		@media @mobile {
		    
		        padding-bottom: 56.25%;
		        width: 100%;
		    
		}
		 @media @tablet  {
		    
		        padding-bottom: 39.25%;
		        width: 520px;
		   
		}
		@media @desktop {
		    
		        padding-bottom: 40.25%;
		        width: 694px;
		   
		}
		@media @desktop-xl {
		   
		        padding-bottom: 33.25%;
		        width: 720px;
		    
		}
		@media @desktop-xl-2 {
		   
		        padding-bottom: 40.25%;
		        width: 858px;
		    
		}
		 @media @desktop-xll  {
		    
		      
		        width: 858px;
		    
		}
		iframe,  object,  embed {
		    position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 100%;
		}
		
	}
	.btn-video {
		
		margin: 80px auto 0;
		webkit-box-shadow: -2px 20px 25px -12px rgba(0, 0, 0, 0.4);
	    -moz-box-shadow: -2px 20px 25px -12px rgba(0, 0, 0, 0.4);
	    box-shadow: -2px 20px 25px -12px rgba(0, 0, 0, 0.4);
	    &:hover {
	    	webkit-box-shadow: -2px 15px 28px -12px rgba(0, 0, 0, 0.4);
	        -moz-box-shadow: -2px 15px 28px -12px rgba(0, 0, 0, 0.4);
	        box-shadow: -2px 15px 28px -12px rgba(0, 0, 0, 0.4);
    
	    }
	    @media @mobile {
	       width: 300px;
	       font-size: 15px;
	    }
	    @media @tablet {
	    	width: 366px;
			margin: 40px auto 0;
			font-size: 16px;
	    }
	    @media @desktop {	
			margin: 80px auto 0;
	    }
	    @media @desktop-xl {
	    	margin: 60px auto 0;   
	    }
	    @media @desktop-xl-2 {
    		margin: 80px auto 0;
	    }
	    @media @desktop-xll {
	    	
	    	
	    }
	}
 
	
}