.cost {
    
    
    
    @media @mobile {
       padding-top: 70px;
        padding-bottom: 70px;
    }
    @media @tablet {
        background: url('@{dir}cost.png') no-repeat top center;  
        padding-top: 265px;
        padding-bottom: 285px;
        margin-top: 0px;
    }
    .container {
        
        @media @mobile {
           padding: 0 5px;
        }
        @media @tablet {
            padding: 0;
        }
        @media @desktop {   
            padding-left: 28px;
        }
        @media @desktop-xl {
            padding-left: 145px;  
        }
    }
    h2 {
        font-size: 35px;
        font-weight: 700;
        // color: #425160; 
        margin-bottom: 30px;
        
         @media @mobile {
            font-size: 30px;
            text-align: center;
        }
        @media @tablet {
            font-size: 35px;
            text-align: left;
        }
    }
    .cost-block {
         
         padding-top: 15px; 
         @media @mobile {
          
    
        }
        @media @tablet {
             background: url('@{dir}cost-bg.png') no-repeat left top; 
            // -webkit-background-size: cover;
            // -moz-background-size: cover;
            // -o-background-size: cover;
            // background-size: cover;
            background-size: 95% 95%;
        }
        @media @desktop {   
            background: url('@{dir}cost-bg.png') no-repeat left top; 
        }
        .item {
            
            @media @mobile {
                width: 309px;
                margin: 0 auto;
                margin-bottom: 60px;
               
            }   
            @media @tablet {
                width: auto;
                margin: 0;
                display: inline-block;
                vertical-align: top;
            }
            h3 {
                position: relative;
                z-index: 1;
                -webkit-border-radius: 24px 24px 10px 10px;
                -moz-border-radius: 24px 24px 10px 10px;
                border-radius: 24px 24px 10px 10px;
                background-color: #fff;
                -webkit-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                -moz-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                box-shadow: 0 21px 18px rgba(166,178,200,.14);

                font-size: 18px;
                font-weight: 500;
                color: #2f4a7a; 
                margin: 0px 0 5px 0;
                // padding: 15px 10px;
                height: 48px; 
                line-height: 47px;
                @media @mobile {
                    
                }
                @media @tablet {
                    display: none;
                }
                
            }
            .list {
                display: inline-block;
                vertical-align: top;
                margin: 0px 3px;
                li {
                    
                    margin: 0px 0 5px 0;
                    font-size: 16px;
                    font-weight: 400;
                    color: #2f4a7a; 
                    @media @mobile {
                        padding: 15px 10px;
                        font-size: 14px;
                    }
                    @media @tablet {
                        padding: 15px 20px;
                        font-size: 15px;
                    }
                    @media @desktop {   
                        font-size: 16px;
                    }
                }
                .li-1 {
                    position: relative;
                    z-index: 1;
                    -webkit-border-radius: 24px 24px 10px 10px;
                    -moz-border-radius: 24px 24px 10px 10px;
                    border-radius: 24px 24px 10px 10px;
                    background-color: #fff;
                    -webkit-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                    -moz-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                    box-shadow: 0 21px 18px rgba(166,178,200,.14);
                    @media @mobile {
                        display: none;
                    }
                    @media @tablet {
                        display: block;
                    }
                }
                .li-2 {
                    -webkit-border-radius: 10px 10px 0 0;
                    -moz-border-radius: 10px 10px 0 0;
                    border-radius: 10px 10px 0 0;
                    background-color: #f2f0f6;
                }
                .li-3 {
                    background-color: #f6f5f9;
                }
                .li-4 {
                    position: relative;
                    z-index: 1;
                    -webkit-border-radius: 0 0 10px 10px;
                    -moz-border-radius: 0 0 10px 10px;
                    border-radius: 0 0 10px 10px;
                    background-color: #fff;
                    -webkit-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                    -moz-box-shadow: 0 21px 18px rgba(166,178,200,.14);
                    box-shadow: 0 21px 18px rgba(166,178,200,.14);
                }
            }
            .list-left {
                li {
                   
                    
                    @media @mobile {
                        width: 240px;
                        height: 48px; 
                    }
                    @media @tablet {
                        width: 298px;
                        height: 48px;
                    }
                    @media @desktop {   
                       
                    }
                }
                

            }
            .list-right {
                text-align: center;
                
                @media @mobile {
                    margin: 0px 0px;
                }
                @media @tablet {
                    margin: 0px 3px;
                }
                li {
                    
                    height: 48px;
                    @media @mobile {
                        width: 59px;
                    }
                    @media @tablet {
                        width: 129px;
                    }
                    @media @desktop {   
                       width: 182px;
                    }
                    
                    img {
                        margin-top: -5px;
                    }
                }
                
            }
            .btn-cost {
                width: 168px;
                height: 44px;
                -webkit-border-radius: 22px;
                -moz-border-radius: 22px;
                border-radius: 22px;
                line-height: 43px;
                margin: 35px auto 0;
                @media @mobile {
                    
                    margin: 25px auto 0;
                }   
                @media @tablet {
                    width: 129px;
                    margin: 35px auto 0;
                }
                @media @desktop {   
                   width: 168px; 
                }
            }
            .btn-cost-mob {
                @media @mobile {
           
                }
                @media @tablet {
                    display: none;
                }
            }
            .btn-cost-dex {
                @media @mobile {
                    display: none;
                }
                @media @tablet {
                    display: block;
                }
            }
            .list-left-mob {
                
                @media @mobile {
           
                }
                @media @tablet {
                    display: none;
                }
            }
        }
    }
}