body {
    background: #f4f9fa;
    position: relative;
    z-index: 1;
    padding-right: 0 !important;
    // overflow-y: hidden;
    max-width: 1920px;
    margin: 0 auto;
}

.btn {
            transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
        -ms-transition: all 0.5s ease;
    outline: none;
}
.btn-orange {
    width: 277px;
    height: 56px;
    -webkit-border-radius: 29px;
    -moz-border-radius: 29px;
    border-radius: 29px;

    background-image: -webkit-linear-gradient(right, #ff8214, #ff681f);
    background-image: -moz-linear-gradient(right, #ff8214, #ff681f);
    background-image: -o-linear-gradient(right, #ff8214, #ff681f);
    background-image: -ms-linear-gradient(right, #ff8214, #ff681f);
    background-image: linear-gradient(to left, #ff681f, #ff8214);

    webkit-box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0.4);
    -moz-box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0.4);
    box-shadow: -2px 20px 25px -12px rgba(244, 118, 107, 0.4);

    font-size: 16px;
    color: #fff;
    font-weight: 500;
    line-height: 55px;
            transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
        -ms-transition: all 0.5s ease;

    &:hover {
        background-image: linear-gradient(to left, #ff8214, #ff681f);
        webkit-box-shadow: -2px 15px 28px -12px rgba(244, 118, 107, 0.4);
        -moz-box-shadow: -2px 15px 28px -12px rgba(244, 118, 107, 0.4);
        box-shadow: -2px 15px 28px -12px rgba(244, 118, 107, 0.4);
    }
    

    @media @mobile {
    }
    @media @tablet {
    }
}
.btn-red {
    color: #fff;
    background-image: -webkit-linear-gradient(bottom, #fe4753, #f93542);
    background-image:    -moz-linear-gradient(bottom, #fe4753, #f93542);
    background-image:      -o-linear-gradient(bottom, #fe4753, #f93542);
    background-image:     -ms-linear-gradient(bottom, #fe4753, #f93542);
    background-image:         linear-gradient(to top, #f93542, #fe4753);

    // -webkit-box-shadow: 0 15px 25px 3px rgba(235,62,98,.27);
    // -moz-box-shadow: 0 15px 26px 3px rgba(235,62,98,.27);
    // box-shadow: 0 15px 26px 3px rgba(235,62,98,.27);
            box-shadow: 0 20px 30px -15px rgba(235,62,98,1);
    -webkit-box-shadow: 0 20px 30px -15px rgba(235,62,98,1);
       -moz-box-shadow: 0 20px 30px -15px rgba(235,62,98,1);
    &:hover {
        background-image: linear-gradient(to top, #fe4753, #f93542);
        -webkit-box-shadow: 0 20px 30px -15px rgba(235,62,98,0);
           -moz-box-shadow: 0 20px 30px -15px rgba(235,62,98,0);
                box-shadow: 0 20px 30px -15px rgba(235,62,98,0);
    }
    span {
        &:after {
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 9px !important;
            height: 9px !important;
            background: #fff;
            margin: 0 0 0 25px;
        }
    }
}



