.time {

    
    @media @mobile {

       background: url('@{dir}bg-mob-2.jpg') no-repeat top center; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover; 

        outline: 1px solid #00e4fc;
        -webkit-outline: 1px solid #00e4fc;
        -moz-outline: 1px solid #00e4fc;
        -o-outline: 1px solid #00e4fc;
        outline-offset: 15px;
        -webkit-outline-offset: 15px;
        -moz-outline-offset: 15px;
        -o-outline-offset: 15px;
        padding: 70px 0;
        overflow: hidden;
    }
    @media @tablet {
        padding: 269px 0 180px 0px ; 
        background: none;
        outline: none;
        outline-offset: 0px;
        overflow: visible;
    }
    @media @desktop {   
        padding: 260px 0 180px 0px ; 
    }
    @media @desktop-xl {
        padding: 240px 0 180px 0px ;     
    }
    @media @desktop-xl-2 {
        padding: 210px 0 180px 0px ;
    }
    @media @desktop-xll {
        
        
    }
    .container {
        position: relative;
        z-index: 1;
    }

        h2 {
            font-size: 35px;
            font-weight: 300;
            // color: #425160; 
            margin-bottom: 10px;
            
            @media @mobile {
                font-size: 30px;
                text-align: center;
            }
            @media @tablet {
                font-size: 27px;
                text-align: left;
            }
            @media @desktop {   
                font-size: 35px;
            }
        }
        h3 {
            
            font-size: 35px;
            font-weight: 700;
            // color: #dbb970; 
            margin-bottom: 15px;
            @media @mobile {
                font-size: 28px;
                text-align: center;
            }
            @media @tablet {
                text-align: left;
                font-size: 27px;
            }
            @media @desktop {   
                font-size: 35px;
            }
        }
        h4 {
            font-size: 20px;
            font-weight: 300;
            // color: #6a7175; 
            margin-bottom: 0px;
            @media @mobile {
                font-weight: 400;
                margin-bottom: 130px;
                text-align: center;
            }
            @media @tablet {
                font-weight: 300;
                margin-bottom: 0px;
                text-align: right;
            }
            @media @desktop {   
                
            }
        }
    .time-left {
        
        
        @media @mobile {
           
        }
        @media @tablet {
            display: inline-block;
            vertical-align: top;
        }
        @media @desktop {   
            
        }
        @media @desktop-xl {
            padding-left: 95px;    
        }
        
    }

    .time-right {
        position: relative;
        
        
        @media @mobile {
           
        }
        @media @tablet {
            float: right;
            text-align: right;
            padding-right: 45px;
        }
        @media @desktop {   
            padding-right: 95px;
        }
       
        h3 {
            
            @media @mobile {
                text-align: center;
            }
            @media @tablet {
                text-align: right;
            }
        }
        
        

        .pattern-2 {
            position: absolute;
            top: -162px;
            
            z-index: -1;
            @media @mobile {
                height: auto;
                top: -125px;
                right: 50%;
                width: 450px;
                margin-right: -225px;
            }
            @media @tablet {
                top: -110px;
                right: 0px;
                width: 400px;
                margin-right: 0px;
            }
            @media @desktop { 
                top: -162px;  
                right: 0px;
                width: auto;
            }
            @media @desktop-xl {
                right: -40px;    
            }
        }
    }
}