.background-3 {
   
    position: relative;
    z-index: 3;
    @media @mobile {
       
    }
    @media @tablet {
         background: url('@{dir}background-3.png') no-repeat top center; 
        // background-position: 50% 50%;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
