.certificates {
    
    @media @mobile {
       background: url('@{dir}bg-mob-2.jpg') no-repeat top center; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover; 

        outline: 1px solid #00e4fc;
        -webkit-outline: 1px solid #00e4fc;
        -moz-outline: 1px solid #00e4fc;
        -o-outline: 1px solid #00e4fc;
        outline-offset: 15px;
        -webkit-outline-offset: 15px;
        -moz-outline-offset: 15px;
        -o-outline-offset: 15px;
        padding: 70px 0;
    }
    @media @tablet {
        background: none;
        outline: none;
        outline-offset: 0px;
        padding: 0;
        margin-top: 180px;
    }

    .container {
        
        position: relative;
        z-index: 0;
        @media @mobile {
           text-align: center;
        }
        @media @tablet {
            text-align: right;
        }
        @media @desktop {   
            padding-right: 100px;
            
        }
    }
    h2 {
        font-size: 35px;
        font-weight: 700;
        // color: #dbb970; 
        margin-bottom: 10px;
        
        @media @mobile {
           font-size: 30px;
           text-align: center;
        }
        @media @tablet {
            font-size: 35px;
            text-align: right;
        }
    }
    h3 {
        font-size: 45px;
        font-weight: 300;
        // color: #425160; 
        margin-bottom: 25px;
        text-align: right;
        @media @mobile {
           font-size: 32px;
           text-align: center;
        }
        @media @tablet {
            font-size: 45px;
            text-align: right;
        }
    }
    p {
        
        display: inline-block;
        vertical-align: top;
        font-size: 16px;
        font-weight: 300;
        // color: #425160; 
        margin-bottom: 0px;
        
        @media @mobile {
           margin-bottom: 30px;
        }
        @media @tablet {
            text-align: right;
            width: 500px;
            margin-bottom: 20px;
        }
        @media @desktop {   
            margin-bottom: 0px;
        }
    }

    .certificates-block {
        position: relative;
        text-align: center;
        @media @mobile {
           
        }
        @media @tablet {

        }
        @media @desktop {   
            width: 815px;
            margin-right: 65px;
            display: inline-block;
            vertical-align: top;
        }
        .item {
            display: inline-block;
            vertical-align: top;
            a {
                outline: none !important;
                img {
                    outline: none !important;
                    margin: 15px 10px;
                    -webkit-box-shadow: 3px 17px 40px rgba(0,0,0,.25);
                    -moz-box-shadow: 3px 17px 40px rgba(0,0,0,.25);
                    box-shadow: 3px 17px 40px rgba(0,0,0,.25);
                    @media @mobile {
                        height: auto;
                    }
                    @media @tablet {
                        width: 200px;
                    }
                    @media @desktop {   
                        width: auto;
                    }
                }
                
            }
        }
        .item-top {
            a {
                img {
                     margin: 15px 19px;
                    @media @mobile {
                        height: auto;
                    }
                    @media @tablet {
                        width: 182px;
                    }
                    @media @desktop {   
                        width: auto;
                    }
                }
            }
            
        }
        .item-1 {
            
            @media @mobile {
                margin-top: 0px;
            }
            @media @tablet {
                margin-top: -50px;
            }
            @media @desktop {   
                
            }
        }
        .item-2 {
            
            @media @mobile {
           
            }
            @media @tablet {
                margin-top: 40px;
            }
        }
        .item-3 {
            
            @media @mobile {
           
            }
            @media @tablet {
                margin-top: 130px;
            }
        }
        .item-4 {
            @media @mobile {
           
            }
            @media @tablet {
                margin-top: -180px;
            }
        }
        .item-5 {
            @media @mobile {
           
            }
            @media @tablet {
                margin-top: -90px;
            }
        }
        
        
    }
    .pattern-4 {
        position: absolute;
        bottom: -100px;
        left: -40px;
        z-index: -1;
        @media @mobile {
            height: auto;
            width: 100%;
            left: 0px;
        }
        @media @tablet {
            width: auto;
            left: -40px;
        }
    }
}